<template>
	<view class="main">
		<view class="item" v-for="item in dataList" :key="item.id">
			<p class="title">{{item.name}} <span class="time">{{item.date}}</span></p>
			<view class="cont" v-if="item.device_have===1">
				<view class="tip" v-if="item.chrominance" :class="item.chrominance_status===1?'yellow':(item.chrominance_status===2?'red':'')">
					<view>色度：</view>
					<view class="num">{{item.chrominance}}</view>
					<em>℃</em>
				</view>
				<view class="tip"  v-if="item.turbidity" :class="item.turbidity_status===1?'yellow':(item.turbidity_status===2?'red':'')">
					<view>浊度：</view>
					<view class="num" >{{item.turbidity}}</view>
					<em>NTU</em>
				</view>
				<view class="tip" v-if="item.COD" :class="item.COD_status===1?'yellow':(item.COD_status===2?'red':'')">
					<view>化学需氧量：</view>
					<view class="num" >{{item.COD}}</view>
					<em>mg/L</em>
				</view>
				<view class="tip" v-if="item.TOC" :class="item.TOC_status===1?'yellow':(item.TOC_status===2?'red':'')">
					<view>有机含碳量：</view>
					<view class="num" >{{item.TOC}}</view>
					<em>mg/L</em>
				</view>
			</view>
			<div class="null-tip" v-else style="margin-top: 30px">暂无数据</div>
		</view>
		<view class="null-tip" v-if="dataList.length===0" >暂无数据</view>
	</view>
</template>

<script>
	import {getResouData} from "@/utils/api/online"
	export default {
		data() {
			return {
				dataList:[]
			}
		},
		onLoad() {
			this.getResouData()
		},
		methods: {
			async getResouData(params){
				let res = await getResouData(params)
				if(res.errno === 0){
					this.dataList = res.data
				}
			},
		}
	}
</script>

<style scoped lang="scss">
	.main{
		display: flex;
		align-items: center;
		flex-direction: column;
		padding-top: 20rpx;
		.item{
			font-family: PingFangSC-Medium;
			border-radius: 16rpx;
			width: 710rpx;
			background-color: #FFFFFF;
			padding: 20rpx 24rpx;
			box-sizing: border-box;
			color: #757575;
			margin-bottom: 20rpx;
			.title{
				display: flex;
				justify-content: space-between;
				align-items: center;
				font-size: 38rpx;
				color: #333333;
				margin-bottom: 20rpx;
				.time{
					font-size: 22rpx;
					color: #8A8A8A;
				}
			}
			.cont{
				line-height: 2;
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				font-size: 24rpx;
				.tip{
					margin-right: 56rpx;
					display: flex;
					align-items: flex-end;
					.num{
						font-size: 48rpx;
						font-weight: 400;
						height: 88rpx;
						line-height: 112rpx;
						em{
							font-style:normal;
							font-size: 16rpx;
						}
					}
				}

			}
		}
	}
	.null-tip{
		margin-top: 80rpx;
		font-size: 32rpx;
	}
</style>
